___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: transparent;
    margin: 0;
}
root{
    --cart-btn-width:10vw;
    --cart-btn-height: 4vh;
}
#content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.top-header {
    height: 10vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 5%;
    align-items: center;
    text-align: center;
    color: black;
    background-color: white;
    border: none;
    box-shadow: 0px 10px 10px rgba(128, 128, 128, 0.221);
}

.top-header-text {
    display: flex;
    justify-content: center;
    align-self: center; /* Center the top-header-text vertically */
    text-align: center;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    font-size: large;
    width: 60vw; /* Adjusted width for better centering */
}
.btn-container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 10vw;
    height:40%;
    position: relative; /* Add relative positioning to the container */
}

.btn-container .cart {
    align-self: center;
    background-color: black;
    color: white;
    border: none;
    border-radius: 5px;
    width: 10vw;
    height: 100%;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    justify-self: right;
}

.btn-container .counter {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: red;
    border: none;
    border-radius: 50%;
    color: white;
    width: 13px;
    height: 13px;
    position: absolute; /* Use absolute positioning */
    top: -6px; /* Adjust the top position */
    right: -6px; /* Adjust the right position */
    text-align: center;
    font-size: 10px;
}


.icon {
    width: 24px;
    margin-left: calc((10% - 24px) / 2);
    margin-right: calc((10% - 24px) / 2);
    aspect-ratio: 1 / 1;
    align-self: center;
    background-color: transparent;
    background-size: cover;
    border: none;
    border-radius: 50%;
}

.grid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap:20px;
    padding:5%;
    width: 60vw;
    height: auto;
}

.position{
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding:5%;
    width:70%;
    aspect-ratio: 1 / 1;
    background-color: whitesmoke;
    border:none;
    border-radius:10px;
    box-shadow: 0px 0px 5px lightgray;
}

.photo{
    background-color: lightgray;
    width:90%;
    height:70%;
    border:none;
    border-radius:5px;
    background-size:cover;
    background-color: transparent;
}

.position-title, .price{
    color:black;
    font-size: large;
    font-weight: bold;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin:5%;
}

.add-btn{
    display:none;
    color:white;
    font-weight: bold;
    background-color: black;
    border:none;
    border-radius: 5px;
    width:30%;
    height:10%;
}

.add-btn.active{
    display:block;
}

.add-btn-container{
    display:none;
    justify-content: space-between;
    align-items: center;
    width:50%;
    height:10%;
}

.add-btn-container.active{
    display: flex;
}

.plus-btn, .minus-btn{
    width:20px;
    aspect-ratio: 1/1;
    background-color: black;
    color:white;
    font-weight: bold;
    box-shadow: 3px 3px 3px lightgray;
    border:none;
    border-radius: 50%;
}

.counter-display{
    width:50%;
    height:100%;
    background-color: lightgray;
    border:none;
    border-radius: 5px;

}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;IACI,6BAA6B;IAC7B,SAAS;AACb;AACA;IACI,qBAAqB;IACrB,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,OAAO;IACP,mBAAmB;IACnB,kBAAkB;IAClB,YAAY;IACZ,uBAAuB;IACvB,YAAY;IACZ,oDAAoD;AACxD;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,kBAAkB,EAAE,0CAA0C;IAC9D,kBAAkB;IAClB,YAAY;IACZ,yCAAyC;IACzC,iBAAiB;IACjB,gBAAgB;IAChB,WAAW,EAAE,wCAAwC;AACzD;AACA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,WAAW;IACX,UAAU;IACV,kBAAkB,EAAE,8CAA8C;AACtE;;AAEA;IACI,kBAAkB;IAClB,uBAAuB;IACvB,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,yCAAyC;IACzC,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,qBAAqB;IACrB,YAAY;IACZ,kBAAkB;IAClB,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB,EAAE,6BAA6B;IACjD,SAAS,EAAE,4BAA4B;IACvC,WAAW,EAAE,8BAA8B;IAC3C,kBAAkB;IAClB,eAAe;AACnB;;;AAGA;IACI,WAAW;IACX,mCAAmC;IACnC,oCAAoC;IACpC,mBAAmB;IACnB,kBAAkB;IAClB,6BAA6B;IAC7B,sBAAsB;IACtB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,QAAQ;IACR,UAAU;IACV,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,UAAU;IACV,SAAS;IACT,mBAAmB;IACnB,4BAA4B;IAC5B,WAAW;IACX,kBAAkB;IAClB,iCAAiC;AACrC;;AAEA;IACI,2BAA2B;IAC3B,SAAS;IACT,UAAU;IACV,WAAW;IACX,iBAAiB;IACjB,qBAAqB;IACrB,6BAA6B;AACjC;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,iBAAiB;IACjB,2DAA2D;IAC3D,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,uBAAuB;IACvB,WAAW;IACX,kBAAkB;IAClB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,8BAA8B;IAC9B,mBAAmB;IACnB,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,UAAU;IACV,iBAAiB;IACjB,uBAAuB;IACvB,WAAW;IACX,iBAAiB;IACjB,iCAAiC;IACjC,WAAW;IACX,kBAAkB;AACtB;;AAEA;IACI,SAAS;IACT,WAAW;IACX,2BAA2B;IAC3B,WAAW;IACX,kBAAkB;;AAEtB","sourcesContent":["body {\r\n    background-color: transparent;\r\n    margin: 0;\r\n}\r\nroot{\r\n    --cart-btn-width:10vw;\r\n    --cart-btn-height: 4